// GET ACTIVE PLAN TYPES ==========================
export const GetPlansTypes = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<PlansTypeInterface[]>(
        `${API.CLAIMING}/session-type`
      );
      console.log({ GET_PLANS_TYPES: res.data });
      let plan_types: PlansTypeInterface[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.length > 0) {
          plan_types = res.data;
        }
      }
      dispatch<GetPlansTypesAction>({
        type: ActionTypes.GET_PLANS_TYPES,
        payload: plan_types,
      });
      callback(false, "");
    } catch (error) {
      console.error({ error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

