// GET ACTIVE PLAN TYPES ==========================
export const getActiveSessionPlan = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<SessionPlanInterface>(
        `${API.CLAIMING}/session/active/`
      );
      console.log({ GET_ACTIVE_SESSION_PLANS: res.data });
      let session_plan: SessionPlanInterface | undefined = undefined;
      // if (res.status === 200) {
      //   // setApplications(res.data);
      // }
      if (res.data && res.data.is_active !== undefined) {
        session_plan = res.data;
      }
      console.log({ session_plan: res.data });

      dispatch<GetActiveSessionPlanAction>({
        type: ActionTypes.GET_ACTIVE_SESSION_PLANS,
        payload: session_plan ? session_plan : "none",
      });
      callback(false, "", session_plan ? session_plan : "none");
    } catch (error) {
      console.error({ error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

